import styled from "styled-components";

export const FooterStyle = styled.div`
width: 100%;
height: 300px;
background: #e9e9e9; 
display: flex;
justify-content: center;
align-items: center;
text-align: center;
`
export const IconStyle = styled.div`
display: flex;
justify-content: center;
align-items: center;
width: 1200px;
height: 100%;

a{
    display: flex;
justify-content: center;
align-items: center;
    padding: 1rem;
    text-decoration: none;
    color: #444;
}
a:hover{
    color:#666;
}
`
